.star__rating{
    margin: 60px 0px 90px 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    & p{
        font-family: 'Open_Sans';
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 150%;
        color: #1D212A;
        padding: 0px 8px;
    }
    .img__vector{
        display: flex;

    }
}
.fill{
    display: flex;
    background-color: #00B67A;
}
.empty{
    background: #DCDCE6;
}



